.main {
	display: flex;
	flex-direction: column;
}

.post {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2vh;
	margin-bottom: 7vh;
}

.post-text {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 95%;
	height: 77px;
	gap: 22vw;
	padding: 2px;
	box-shadow: 0px 0px 0px 0.3px black;
}

.post-image {
	max-width: 75vw;
	max-height: 50vh;
	box-shadow: 2px 2px 2px 2px black;
}

.post-image-block {
	padding: 0.5vw;
	width: 100%;
	background-color: rgba(128, 128, 128, 0.212);
}

.like {
	width: 2.2vw;
	transition: all 0.5s;
}

.like:hover {
	transform: scale(1.1) rotate(360deg);
}

.post-author-block {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-left: 15px;
	border-left: 7px outset rgb(57, 179, 201);
	height: 75px;
}

.post-author {
	display: flex;
	gap: 1vw;
	align-items: center;
}

.post-author-name-block {
	display: flex;
	gap: 0.5vw;
}

.post-author-login {
	opacity: 0.5;
}

.post-author-img {
	max-width: 3vw;
	max-height: 2.5vh;
}

.post-like-block {
	display: flex;
	align-items: center;
	gap: 10px;
	height: 75px;
}

